.full-balloon {
  overflow-y: scroll;
  padding: 2rem 1rem;
}

.form-full-bottom {
  display: flex;
  flex-direction: column;
  margin-top: 2.5rem;
}

.form-property, .form-input-bottom {
  display: flex;
  flex-direction: row;
}

.form-property button {
  flex: 0 0 auto; /* flex-grow: 0, flex-shrink: 0, flex-basis: auto */
}

.form-field {
  flex: 1; /* form-field가 가능한 많은 공간을 차지하도록 설정 */
}

/* Button의 크기가 너무 커지지 않도록 조절 */
.form-property button, .form-input-bottom button {
  flex: 0 0 auto; /* flex-grow: 0, flex-shrink: 0, flex-basis: auto */
}
